<template>
  <div class="root">
    <v-head :showHeader="showHeader"></v-head>

    <!--<div>module: {{ module }} subModule: {{ curSubModuleIndex }} curItemIndex: {{ searchType }}</div>-->

    <div class="home_container">
      <!-- 大分类 -->
      <div class="top_container">
        <div :class="['base_module', module === 0 ? 'active_module' : 'inactive_module']" @click="selectSuperSearchModule">
          <span>基础查询</span>
        </div>
        <div :class="['base_module', module === 1 ? 'active_module' : 'inactive_module']" @click="selectThirdModule">
          <span>第三方查询</span>
        </div>
      </div>


      <!-- 中间分类: 3方接口 -->
      <div v-show='isThirdMode' :class="['middle_base', 'middle_3rd']">
        <div class="row_container1">
          <div class="row_container_dbox" v-for="(item, idx) in rowList1" @click="clickSubModule(1, idx)">
            <div class='item' :class="[isSelectSubModule(1, idx) ? 'activeBG' : 'inactiveBG']">
              <img class="img" :src="isSelectSubModule(1, idx) ? item.imgSelSrc : item.imgSrc">
              <div :class="isSelectSubModule(1, idx) ? 'selTextColor' : 'unselTextColor'">{{ item.title }}</div>
            </div>
          </div>
        </div>

        <div class="row_container2">
          <div class="row_container_dbox" v-for="(item, idx) in rowList2" @click="clickSubModule(2, idx)">
            <div class='item' :class="[isSelectSubModule(2, idx) ? 'activeBG' : 'inactiveBG']">
              <img class="img" :src="isSelectSubModule(2, idx) ? item.imgSelSrc : item.imgSrc">
              <div :class="isSelectSubModule(2, idx) ? 'selTextColor' : 'unselTextColor'">{{ item.title }}</div>
            </div>
          </div>
        </div>
      </div>

      <!-- 中间分类: 超级查询 -->
      <div v-show='!isThirdMode' :class="['middle_base', 'middle_super']">
      </div>

      <!-- 底部分类 -->
      <div v-show='isThirdMode' class="typeModule">
        <span class="mini_item" :class="isSelectedType(type) ? 'selText' : 'unSelText'"
              v-for="(type, index) in getCurSubModuleData.data"
              @click="clickType(type)">
          {{ getItemTitle(type) }}
        </span>
      </div>

      <div class="home-search-container">
        <common-search-bar class="home-search-bar" :initSearchType="searchType"></common-search-bar>
      </div>
    </div>

  </div>
</template>

<script>
  import vHead from '../common/Header.vue'
  import { mapState } from 'vuex'
  import CommonSearchBar from '../common/CommonSearchBar.vue'

  const MAX_SUBMODULE_PER_ROW = 5

  export default {
    components: {
      'common-search-bar': CommonSearchBar,
      'v-head': vHead
    },
    data () {
      return {
        searchInput: '',
        rowList1: [],
        rowList2: [],
        curSubModuleIndex: 0,   // 子分类
        msg: 0,
        showHeader: false
      }
    },
    created () {
      this.rowList1 = gconst.SearchGroup.slice(1, 1+MAX_SUBMODULE_PER_ROW)
      this.rowList2 = gconst.SearchGroup.slice(1+MAX_SUBMODULE_PER_ROW)
      console.log("tzt-----------created module:" + this.module + " moduleSearchType:" + this.moduleSearchType + " searchType:" + this.searchType)
    },
    mounted() {
      gstorage.reload()
      this.updateBySearchType()
    },
    computed: {
      ...mapState('search', [
        'module',       // 大分类
        'moduleSearchType',
        'searchType'
      ]),

      isThirdMode () {
        return this.module === 1
      },

      getCurSubModuleData () {
        return this.getSubModuleData(this.curSubModuleIndex)
      }
    },
    watch: {
        searchType: {
          handler (newValue, oldValue) {
            console.log("------searchType old:" + oldValue + " new:" + newValue)

            this.updateBySearchType()
          },
          deep: true
      }
    },
    methods: {
      // 【基础查询】模块
      selectSuperSearchModule () {
        let moduleSearchType = gconst.SearchType.SUPER_SEARCH
        this.$store.dispatch('search/setModuleAndModuleSearchType', {
          module: 0,
          moduleSearchType: this.moduleSearchType,
        })
        this.$store.dispatch('search/setSearchType', moduleSearchType)
      },

      // 【第三方查询模块】模块
      selectThirdModule () {
        let search_type
        if (this.moduleSearchType === gconst.SearchType.SUPER_SEARCH)
          search_type = this.rowList1[0].data[0]
        else
          search_type = this.moduleSearchType

        this.$store.dispatch('search/setModuleAndModuleSearchType', {
          module: 1,
          moduleSearchType: search_type,
        })
        this.$store.dispatch('search/setSearchType', search_type)
        this.updateBySearchType()
      },

      // 选中【第三方查询】下的子模块
      clickSubModule (row, index) {
        this.curSubModuleIndex = (row - 1) * 5 + index
        let md = this.getSubModuleData(this.curSubModuleIndex)

        if (md.data && md.data.length > 0 && !md.data.includes(this.moduleSearchType)) {
          let search_type = md.data[0]
          this.$store.dispatch('search/setModuleAndModuleSearchType', {
            module: 1,
            moduleSearchType: search_type,
          })
          this.$store.dispatch('search/setSearchType', search_type)
        } else {
          this.$store.dispatch('search/setModuleAndModuleSearchType', {
            module: 1,
            moduleSearchType: this.moduleSearchType,
          })
          this.$store.dispatch('search/setSearchType', this.moduleSearchType)
        }
      },

      // 根据查询类型，获得模块、子模块
      updateBySearchType () {

        if (this.module === 0) {
          this.$store.dispatch('search/setSearchType', gconst.SearchType.SUPER_SEARCH)
        } else if (this.module === 1) {

          for (let idx in this.rowList1) {
            let item = this.rowList1[idx]
            if (item && item.data.includes(this.moduleSearchType)) {
              this.curSubModuleIndex = parseInt(idx)
              break
            }
          }

          for (let idx in this.rowList2) {
            let item = this.rowList2[idx]
            if (item && item.data.includes(this.moduleSearchType)) {
              this.curSubModuleIndex = 5 + parseInt(idx)
              break
            }
          }

          this.$store.dispatch('search/setSearchType', this.moduleSearchType)
        }


      },

      // 子模块数据
      getSubModuleData (sub_module_idx) {
        if (sub_module_idx >= MAX_SUBMODULE_PER_ROW) {
          return this.rowList2[sub_module_idx - MAX_SUBMODULE_PER_ROW]
        } else {
          return this.rowList1[sub_module_idx]
        }
      },


      // 是否选中模块
      isSelectSubModule (row, index) {
        let idx = (row - 1) * 5 + index
        return this.curSubModuleIndex === idx
      },

      isSelectedType (type) {
        if (this.searchType === type)
          return true
        return false
      },

      // 选中子项
      clickType (type) {
        this.$store.dispatch('search/setModuleAndModuleSearchType', {
          module: 1,
          moduleSearchType: type,
        })
        this.$store.dispatch('search/setSearchType', type)
      },

      getItemTitle (item_type) {
        let dt = gddmgr.getScreenData(item_type)
        return dt.title || '未知标题'
      },

    },
  }
</script>

<style>
  .root {
    margin: 0 auto;
    height: 100%;
  }
  .home_container {
    margin: 0 auto 80px;
    padding: 30px 160px 60px;
    height: calc(100% - 80px);
    overflow-y: scroll;
  }
  .top_container, .middle_base, .typeModule, .home-search-container {
    margin: 0 auto;
    width: 90%;
  }

  .top_container {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }

  .base_module {
    font-size: 20px;
    width: 145px;
    height: 50px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .inactive_module,  .active_module {
    font-family: MicrosoftYaHei;
    cursor: pointer;
  }
  .active_module {
    color: #666666;
    background: #F6F6F6;
  }

  .inactive_module {
    font-size: 20px;
    color: #373A41;
  }

  .middle_base {
    background: rgba(246,246,246,1);
    box-shadow: 0 0 10px rgba(0,0,0,0.1);
    border-radius: 10px;
  }

  .middle_super {

  }

  .middle_3rd {
    display: flex;
    flex-direction: column;
    overflow: auto;
  }

  .row_container1, .row_container2 {
    flex: 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
  }

  .row_container1 .row_container_dbox, .row_container2 .row_container_dbox {
    margin: 30px 0;
  }

  .activeBG {
    background: #78A7FF !important;
  }

  .inactiveBG {
    bacground: white !important;
  }
  .home_container .item {
    margin: 0 20px;
    padding: 10px 0 15px;
    background: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0,0,0,.1);
    cursor: pointer;
  }

  .home_container .item  .img {
    display: inline-block;
    width: 90%;
  }

  .selTextColor {
    color: white;
  }

  .unselTextColor {
    display: inline;
    color: #878787;
    font-size: 14px;
    box-sizing: border-box;
  }

  .selText {
    color: #78A7FF;
  }
  .unSelText {
    color: #96979B;
  }

  .mini_item {
    display: inline-block;
    text-align: center;
    margin: 30px 10px 0px 10px;
    cursor: pointer;
    min-width: 100px;
  }

  <!-- 最小分类 -->
  .typeModule {
    height: 200px;
  }

  .root .input_search {
    margin-top: 40px;
  }
  .root .el-input-group__prepend {
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    background-color: rgba(70,153,255,1);
    width: 125px;
  }
  .root .el-input-group__append {
    background-color: rgba(70,153,255,1);
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
  }
  .root .input_search div {
    height: 58px;
    border: none;
    color: #ffffff;
    font-size: 16px;
    font-family: MicrosoftYaHei;
  }
  .root .input_search input {
    height: 60px;
  }
  .root .search_method {
    cursor: inherit;
  }
  .root .search_click_button {
    width: 90px;
  }
  .root .input_search .el-input__inner {
    border-top: 1px solid #96979B;
    border-bottom: 1px solid #96979B;
  }

  .home-search-container {
    display: flex;
    flex: 1;
    height: 100px;
    min-width: 300px;
  }

  /*
    头部中的搜索框样式
  */

  .home-search-container .select-type {
    border: none;
    height: 50px;
    line-height: 50px;
    box-sizing: border-box;
    background: #4699FF;

  }
  .home-search-container .select-type .el-input--medium .el-input__inner {
    height: 40px;
    line-height: 40px;
    background: #4699FF;
    color: #FFFFFF;
    border: none;
    border-radius: 0;
    font-size: 16px;
  }
  .home-search-container .search_bar_root .row_container  {
    border-top: 1px solid #4778C7;
    border-bottom: 1px solid #4778C7;
    height: 50px;
    line-height: 50px;
    box-sizing: border-box;
  }
  .home-search-container .search_bar_root .button {
    border-radius: 0;
    height: 50px;
  }

  .home-search-container .search_bar_root .row_container .el-input--medium .el-input__inner {
    border-radius: 0;
    border: none;
    border-right: 2px solid #666666;
  }
  .home-search-container .search_bar_root .row_container .el-col:nth-last-child(1) .el-input--medium .el-input__inner {
    border: none;
  }

  .home-search-container .search_bar_root .button span {
    font-size: 16px;
  }
  /*
    end
  */

  .home-search-bar {
    flex: 1;
    width: 100%;
  }

</style>



